<template>
  <div class="downList">
    <div class="nofile" v-if="download.length === 0">暂无数据</div>
    <div class="havefile" v-if="download.length !== 0">
      <div
        class="leftNavBtn"
        v-for="(item, index) in download"
        :key="index"
      >
        <p
          style="
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            line-height: 18px;
            font-size: 14.4px;
          "
        >
          {{ item.downloadFileName }}
        </p>
        <el-progress :percentage="item.percent"></el-progress>
        <p
          style="
            text-align: right;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            line-height: 19px;
            font-size: 14.4px;
            margin-right: 3px;
          "
        >
          {{ item.netSpeed }}/s
        </p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  props: ["download"],
  watch: {},
  methods: {},
  mounted() {
    let protocol = window.location.protocol; //协议
    let host = window.location.host; //主机
    this.hostUrl = `${protocol}//${host}`;
  },
};
</script>
<style scoped>
</style>



